<template>
  <div>
    <crumbs menu='商场管理' subMenu='场所信息' /> 
    <el-card>
      <template #header>
        <el-row>
          <el-col :span='2'>
            <span style="line-height:40px;">商场名称:</span>
          </el-col>
          <el-col :span='12'>
            <el-col :span='12'>
                <el-select v-model="marketName" placeholder="请选择" clearable>
                  <el-option
                    v-for="item in marketList"
                    :key="item"
                    :label="item"
                    :value="item">
                  </el-option>
                </el-select>
            </el-col>
            <el-col :span='12' :offset="1">
                <el-button @click="editVisible2=true">添加数据</el-button>
            </el-col>
            
          </el-col>
          <el-col :span='9' style="display:flex;justify-content:flex-end;">
            <!-- <el-button-group> -->
              <el-button type="primary" icon="el-icon-search" @click='onQuery'>查询</el-button>
              <el-button type="primary" icon="el-icon-refresh-right" @click="reSet">重置</el-button>
              <export-excel :file-name="_fileName" :header="tableSet.map(item=>item.label)"
              :filter-filed="tableSet.map(item=>item.value)" :data="exportTable">导出</export-excel>
            <!-- </el-button-group> -->
          </el-col>
        </el-row>
      </template>
      <el-table
        :data="tableData.filter(data => !marketName || data.marketName.toLowerCase().includes(marketName.toLowerCase())).slice((currentPage - 1) * pagesize, currentPage * pagesize)"
        border
        class="table"
        row-key="id"
        ref="multipleTable"
        header-cell-class-name="table-header"
        @selection-change="handleSelectionChange"
        @sort-change="sortChange"
        :current-page="currentPage"
        stripe
      >
        <el-table-column type="index" label="序号" width="100" align="center">
        </el-table-column>
        <el-table-column
          v-for="i in tableSet"
          :key="i"
          :prop="i.value"
          :label="i.label"
          align="center"
          :sortable="i.label == '总流量' ? true : false"
        ></el-table-column>
        <el-table-column label="操作" width="200" align="center">
          <template #default="scope">
            <el-button  @click="handleEdit(scope.$index, scope.row)"
            size="mini"
              >编辑</el-button
            >
            <el-button
              type="danger"
              size="mini"
              @click="handleDelete(scope.$index, scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[2, 5, 10]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="tableData.filter(data => !marketName || data.marketName.toLowerCase().includes(marketName.toLowerCase())).length"
        >
        </el-pagination>
      </div>
    </el-card>
    <!-- 编辑弹出框 -->
    <el-dialog title="编辑" v-model="editVisible" width="30%">
      <el-form ref="form" :model="form" label-width="130px">
        <el-form-item label="场所名称：">
          <el-input v-model="form.areaName"
            style="width:53%;"
          ></el-input>
        </el-form-item>
        <el-form-item label="场所面积：">
          <el-input v-model="form.areaSize" 
            style="width:53%;"
          ></el-input>
        </el-form-item>
        <el-form-item label="店铺名称：">
          <el-input v-model="form.shop"
            style="width:53%;"
          ></el-input>
        </el-form-item>
        <el-form-item label="店铺类型:">
          <el-select v-model="form.type" placeholder="请选择">
            <el-option
              v-for="item in typeList"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商场名称:">
          <el-select v-model="form.marketName" placeholder="请选择">
            <el-option
              v-for="item in marketList"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商场所属集团:">
          <el-select v-model="form.marketGroup" placeholder="请选择">
            <el-option
              v-for="item in groupList"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属大区:">
          <el-select v-model="form.regionalinfo" placeholder="请选择">
            <el-option
              v-for="item in regionalinfoList"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="primary" @click="saveEdit">确 定</el-button>
          <el-button @click="cancelEdit">取 消</el-button>
        </span>
      </template>
    </el-dialog>
    <!-- 编辑添加 -->
    <el-dialog title="编辑" v-model="editVisible2" width="30%" style="padding-left:30px;">
      <el-form ref="form" :model="addForm" label-width="130px">
        <el-form-item label="场所所属楼栋:">
          <el-select v-model="addForm.building" placeholder="请选择">
            <el-option
              v-for="item in buildingList"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="场所所属楼层:">
          <el-select v-model="addForm.floor" placeholder="请选择">
            <el-option
              v-for="item in floorList"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="场所名称：">
          <el-input v-model="addForm.areaName"
            style="width:53%;"
          ></el-input>
        </el-form-item>
        <el-form-item label="场所面积：">
          <el-input v-model="addForm.areaSize" 
            style="width:53%;"
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label="店铺名称：">
          <el-input v-model="addForm.shop"
            style="width:53%;"
          ></el-input>
        </el-form-item>
        <el-form-item label="店铺类型:">
          <el-select v-model="addForm.type" placeholder="请选择">
            <el-option
              v-for="item in typeList"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="商场名称:">
          <!-- <el-select v-model="addForm.marketName" placeholder="请选择"> -->
            <!-- <el-option
              v-for="item in marketList"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select> -->
          {{choose.marketName}}
        </el-form-item>
        <el-form-item label="商场所属集团:">
          <!-- <el-select v-model="addForm.marketGroup" placeholder="请选择">
            <el-option
              v-for="item in groupList"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select> -->
          {{choose.marketGroup}}
        </el-form-item>
        <el-form-item label="所属大区:">
          <!-- <el-select v-model="addForm.regionalinfo" placeholder="请选择">
            <el-option
              v-for="item in regionalinfoList"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select> -->
          {{choose.regionalinfo}}
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="primary" @click="addData">确 定</el-button>
          <el-button @click="cancelAdd">取 消</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>


<script>
// import { fetchData } from "../api/index";
import ExportExcel from '../pages/excel/ExportExcel'
import Crumbs from '../pages/Crumbs'
import axios from 'axios'
export default {
  name: "marketInfo",
  data() {
    return {
      timeRange:[],
      marketName:'群光广场',
      marketList:[],
      groupList:[],
      regionalinfoList:[],
      buildingList:[],
      floorList:[],
      typeList:[],
      queryMarket: "",
      tableData:[],
      choose:{
        marketName:'',
        marketGroup:'',
        regionalinfo:''
      },
      tableSet: [{
          value: "areaName",
          label: "场所名称",
        },
        {
          value: "areaSize",
          label: "场所面积",
        },
        {
          value: "building",
          label: "所属楼栋",
        },
        {
          value: "floor",
          label: "所属楼层",
        },
        {
          value: "shop",
          label: "入驻店铺",
        },
        {
          value: "type",
          label: "店铺类型",
        },
        {
          value: "marketName",
          label: "商场名称",
        },
        {
          value: "marketGroup",
          label: "商场所属集团",
        },
        {
          value: "regionalinfo",
          label: "所属大区信息",
        }
      ],
      marketData: [
        {
          value: "qggc",
          marketname: "群光广场",
          marketgroup: "蓝天集团",
          regionalinfo: "华中大区",
          businesshours: "14",
          totalflow: 123,
          date: "2020-1-1",
        },
        {
          value: "M+",
          marketname: "M+购物广场",
          marketgroup: "长江实业",
          regionalinfo: "华中大区",
          businesshours: "15",
          totalflow: 221,
          date: "2020-1-1",
        },
        {
          value: "ytcyc",
          marketname: "银泰创意城",
          marketgroup: "ali",
          regionalinfo: "华中大区",
          businesshours: "11",
          totalflow: 255,
          date: "2020-1-1",
        },
        {
          value: "sdgc",
          marketname: "时代商场",
          marketgroup: "汉龙实业",
          regionalinfo: "华中大区",
          businesshours: "12",
          totalflow: 87,
          date: "2020-1-1",
        },
        {
          value: "zbcc",
          marketname: "中百仓储",
          marketgroup: "中百集团",
          regionalinfo: "华中大区",
          businesshours: "15",
          totalflow: 231,
          date: "2020-1-1",
        },
        {
          value: "zsbh",
          marketname: "中商百货",
          marketgroup: "中商集团",
          regionalinfo: "华中大区",
          businesshours: "16",
          totalflow: 222,
          date: "2020-1-1",
        },
      ],
      marketDataCache: '',
      searchTableData: [],
      multipleSelection: [],
      delList: [],
      editVisible: false,
      editVisible2:false,
      form: {},
      idx: -1,
      currentPage: 1, //当前页
      pagesize: 5, //每页数量
      total: 0, //
      addForm:{
        building:'',
        floor:'',
        marketName:'',
        marketGroup:'',
        regionalinfo:'',
        areaName:'',
        areaSize:'',
        shop:'',
        type:''
      }
    };
  },
  // created() {
  //     this.getData();
  // },\
  mounted() {
    // this.total = this.marketData.length;
    axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getLocalInfo').then(res=>{
      console.log(res.data.list)
      this.tableData=res.data.list
      this.marketList=res.data.marketName
      this.regionalinfoList=res.data.regionalinfo
      this.groupList=res.data.marketGroup
      this.typeList=res.data.type
      this.buildingList=res.data.building
      this.floorList=res.data.floor
      this.choose.marketName=this.marketName
      this.choose.marketGroup=this.tableData.find(item=>item.marketName==this.marketName).marketGroup
      this.choose.regionalinfo=this.tableData.find(item=>item.marketName==this.marketName).regionalinfo
      this.addForm.marketGroup=this.choose.marketGroup
      this.addForm.regionalinfo=this.choose.regionalinfo
      this.addForm.marketName=this.choose.marketName
    })
    
  },
  methods: {
    // 获取 easy-mock 的模拟数据
    // getData() {
    //     fetchData(this.query).then(res => {
    //         console.log(res);
    //         this.tableData = res.list;
    //         this.pageTotal = res.pageTotal || 50;
    //     });
    // },

    // 删除操作
    handleDelete(index,row) {
      // 二次确认删除
      console.log(row)
      this.$confirm("确定要删除这条记录吗？", "提示", {
        type: "warning",
      })
        .then(() => {
        let find=this.tableData.findIndex(item=>item.marketName==row.marketName&&item.marketGroup==row.marketGroup&&item.regionalinfo==row.regionalinfo)
        console.log(find)
          this.tableData.splice(find,1);
          this.total = this.tableData.filter(data => !this.marketName || data.marketName.toLowerCase().includes(this.marketName.toLowerCase())).length;
        })
        .catch(() => {});
    },
    // 编辑操作
    handleEdit(index, row) {
      console.log(row)
      // this.timeRange=[this.$moment(row.businessTime.split('-')[0],'HH:mm'),this.$moment(row.businessTime.split('-')[1],'HH:mm')]
      console.log(this.timeRange)
      this.idx = index;
      this.marketDataCache = JSON.stringify(row) ;
      this.form = row;
      this.editVisible = true;
    },
    addData(){
      this.tableData.unshift(this.addForm)
      this.addForm={
        marketName:'',
        marketGroup:'',
        regionalinfo:'',
        areaName:'',
        areaSize:'',
        shop:'',
        type:''
      }
      this.editVisible2 = false;
      this.$message.success(`添加成功`);
    },
    cancelAdd(){
      this.addForm={
        marketName:'',
        marketGroup:'',
        regionalinfo:'',
        areaName:'',
        areaSize:'',
        shop:'',
        type:''
      }
      this.editVisible2 = false;
    },
    // 取消编辑
    cancelEdit() {
      console.log(this.marketDataCache);
      this.editVisible = false;
      this.tableData[this.idx] =JSON.parse(this.marketDataCache) ;
      this.marketDataCache ='';
      //   this.marketData = this.marketData;
    },
    // 保存编辑
    saveEdit() {
      this.editVisible = false;
      this.form.businessTime=this.$moment(this.timeRange[0]).format('HH:mm')+'-'+this.$moment(this.timeRange[1]).format('HH:mm')
      this.$message.success(`修改第 ${this.idx + 1} 行成功`);
      this.tableData[this.idx] = this.form;
      this.marketDataCache ='';
    },
    // 排序
    sortChange(params) {
      //   console.log(params.prop);
      if (params.order == "ascending") {
        this.tableData.sort((a, b) => a[params.prop] - b[params.prop]);
      } else if (params.order == "descending") {
        this.tableData.sort((a, b) => b[params.prop] - a[params.prop]);
      } else {
        this.tableData.sort((a, b) => a.flowRank - b.flowRank);
      }
    },
    onQuery(){
      axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getLocalInfo').then(res=>{
      console.log(res.data.list)
      this.tableData=res.data.list
      this.marketList=res.data.marketName

    })
    },
    reSet(){
      this.marketName=''
      this.onQuery()
    },
    // 分页导航
    handleSizeChange(val) {
      this.pagesize = val;
      console.log(this.pagesize);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
  },
  components:{
    crumbs:Crumbs,
    ExportExcel
    
  },
  computed:{
    exportTable(){
      return this.tableData.filter(data => !this.marketName || data.marketName.toLowerCase().includes(this.marketName.toLowerCase()))
    },
    _fileName(){
      return `场所信息-${this.$moment().format('YYYYDDMM')}`
    }
  },
  watch:{
    marketName(val){
      this.choose.marketName=val
      this.choose.marketGroup=this.tableData.find(item=>item.marketName==val).marketGroup
      this.choose.regionalinfo=this.tableData.find(item=>item.marketName==val).regionalinfo
      this.addForm.marketGroup=this.choose.marketGroup
      this.addForm.regionalinfo=this.choose.regionalinfo
      this.addForm.marketName=this.choose.marketName
    }
  }
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.handle-select {
  width: 120px;
}

.handle-input {
  width: 300px;
  display: inline-block;
}
.table {
  width: 100%;
  font-size: 14px;
}
.red {
  color: #ff0000;
}
.mr10 {
  margin-right: 10px;
}
.table-td-thumb {
  display: block;
  margin: auto;
  width: 40px;
  height: 40px;
}
</style>
